/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #666;
	background-color: #f4f4f4;
	border: 1px solid #e1e1e1;
	border-radius: 0;

}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: .3rem;
}

textarea {
	padding-left: 3px;
	width: 100%;
}

fieldset {
	border: 0;
	padding: 0;
	padding-bottom: .75rem;
	margin: 0;
}

input[type=search] {
	box-sizing: content-box;
}

button, input[type=button], input[type=reset], input[type=submit] {
	background: #248a83;
	border: none;
	border-radius: 0;
	color: #fff;
	font-family: "Novecento Sans Wide", sans-serif;
	font-size: 1rem;
	padding: .75rem 1.5rem;
	text-transform: uppercase;
	text-shadow: none;
	box-shadow: none;
	line-height: 1;
	font-size: .9rem;
}

.white-button {
	@include white-button;
}

.green-button {
	@include green-button;
}